import axios from 'axios';
import { http } from '../utils/http';

export const schoolEnquiry = (payload) => {
    return new Promise((resolve, reject) => {
        http(process.env.REACT_APP_ROOT_URL_SCHOOL, 'POST', 'schools-service/schools/enquiry', payload, true)
            .then((data) => {
                resolve(data);
            })

            .catch((err) => {
                reject(err);
            });
    });
};


export const schoolAddressUpdateDefault = (userId,profileId,payload) => {
    return new Promise((resolve, reject) => {
        http(process.env.REACT_APP_ROOT_URL, 'PUT', `users-service/users/${userId}/profiles/${profileId}/addresses/default`, payload, true)
            .then((data) => {
                resolve(data);
            })

            .catch((err) => {
                reject(err);
            });
    });
};
